.slider {
  width: 150px;
  position: relative;
  margin: auto;
  height: 90px; }
  .slider__wrapp {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center; }
  .slider__item img {
    -o-object-fit: cover;
       object-fit: cover;
    margin-top: 20px; }
  .slider__previous, .slider__next {
    cursor: pointer;
    position: absolute;
    top: 50%;
    width: auto;
    margin-top: -22px;
    padding: 16px;
    color: #111;
    font-weight: bold;
    font-size: 16px;
    transition: 0.6s ease;
    border-radius: 0 2px 2px 0; }
  .slider__next {
    right: 0;
    border-radius: 2px 0 0 2px; }
  .slider__previous:hover, .slider__next:hover {
    background-color: rgba(0, 0, 0, 0.2); }
  .slider__item {
    -webkit-animation-name: fade;
            animation-name: fade;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s; }

@-webkit-keyframes fade {
  from {
    opacity: 0.4; }
  to {
    opacity: 1; } }

@keyframes fade {
  from {
    opacity: 0.4; }
  to {
    opacity: 1; } }

@media (min-width: 768px) {
  .slider {
    height: 100px; } }
